import React, {useState} from 'react'
import {Button, InputNumber, Space, Table, Tabs} from 'antd'
import PlayerModel from 'stores/models/PlayerModel'
import {SizeType} from 'antd/es/config-provider/SizeContext'

export const ResourceType = [
  {name: '골드', value: 'GP'},
  {name: '다이아', value: 'Diamond'},
  {name: '캐릭터경험치', value: 'ExpPoints'},
  {name: '영혼석', value: 'SoulStone'},
  {name: '럭키코인', value: 'LuckyCoin'},
]

const ReturnResource: React.FC<{data: any; currentPlayer: PlayerModel}> = ({
  data,
  currentPlayer,
}) => {
  const [value, onChange] = useState<string | number>()
  const [msg, setMsg] = useState<string>('')
  const {type, count, value: rValue} = data

  const returnResource = () => {
    if (window.confirm(`${type} (${value}) 재화 회수가 맞습니까?`)) {
      currentPlayer.returnResource({[rValue]: value}).then(() => {
        setMsg('회수 우편 발송 완료')
      })
    }
  }

  return (
    <Space>
      <InputNumber
        value={value}
        max={count}
        onChange={onChange}
        style={{color: '#ff4d4f'}}
        formatter={(value) => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
        parser={(value: string | undefined) =>
          (value || '').replace(/(,*)/g, '')
        }
      />
      <Button type="primary" danger onClick={returnResource}>
        재화 회수
      </Button>
      <div style={{color: '#ff4d4f'}}>{msg}</div>
    </Space>
  )
}

const Resources: React.FC<{
  currentPlayer?: PlayerModel
  userResources: any
  size?: SizeType
}> = ({currentPlayer, userResources = {}, size}) => {
  const data = ResourceType.map((resource, idx: number) => ({
    number: idx + 1,
    key: idx + 1,
    type: resource.name,
    count: userResources[resource.value],
    value: resource.value,
  }))

  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: '재화 종류', dataIndex: 'type'},
    {title: '보유 수량', dataIndex: 'count'},
    {
      title: '비고',
      dataIndex: '',
      render: (_: any, data: any) =>
        currentPlayer && (
          <ReturnResource data={data} currentPlayer={currentPlayer} />
        ),
    },
  ]
  return (
    <div>
      <Tabs defaultActiveKey="1" onChange={() => {}} size={size}>
        <Tabs.TabPane tab="보유 재화" key="보유 재화">
          <Table columns={columns} dataSource={data} size={size} />
        </Tabs.TabPane>
      </Tabs>
    </div>
  )
}

export default Resources
